.publications-page {
  min-height: 100vh;
  background-color: #f9fafb;
}

.container {
  max-width: 100%;
  margin: 20px 30px;
  padding: 2rem 1rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
@media (max-width: 768px) {
  .container {
    max-width: 95%;
    padding: 1.5rem;
  }

  .header {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .header h1 {
    font-size: 1.5rem; /* Smaller title */
  }

  .sort-container {
    display: none; /* ✅ Hide sorting on small screens */
  }

  /* ✅ Stack sidebar and main content */
  .content-layout {
    flex-direction: column;
    gap: 1rem;
  }

  .sidebar {
    display: none; /* ✅ Hide filter section */
  }

  .main-content {
    width: 100%;
  }

  /* ✅ Reduce publication sizes */
  .publication-card {
    padding: 1rem;
  }

  .publication-title h2 {
    font-size: 1.2rem;
  }

  .publication-meta {
    font-size: 0.875rem;
  }

  .search-input {
    font-size: 0.875rem;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 80%;
    padding: 3rem;
  }

  .header h1 {
    font-size: 2.5rem; /* Bigger title */
  }

  .content-layout {
    gap: 3rem; /* More spacing */
  }

  .sidebar {
    width: 22rem;
  }

  .main-content {
    flex: 2;
  }

  .publication-title h2 {
    font-size: 2rem;
  }

  .publication-meta {
    font-size: 1.1rem;
  }

  .search-input {
    font-size: 1rem;
  }

  .publication-card {
    padding: 2rem;
  }
}


.header h1 {
  font-size: 1.875rem;
  font-weight: bold;
}

.sort-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.sort-select {
  padding: 0.375rem 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  background-color: white;
}

.content-layout {
  display: flex;
  gap: 2rem;
}

.sidebar {
  width: 16rem;
  flex-shrink: 0;
}

.filter-card {
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  padding: 1.5rem;
}

.search-container {
  position: relative;
  margin-bottom: 1.5rem;
}

.search-input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  padding-left: 2.5rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
}

.search-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
}

.reset-button {
  color: #2563eb;
  font-size: 0.875rem;
  margin-bottom: 1.5rem;
  cursor: pointer;
  border: none;
  background: none;
}

.reset-button:hover {
  color: #1d4ed8;
}

.filter-section {
  margin-bottom: 1.5rem;
}

.filter-section h3 {
  font-weight: 600;
  margin-bottom: 0.75rem;
}

.checkbox-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.keyword-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.keyword-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.keyword-dot {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}

.main-content {
  flex: 1;
}

.publication-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.publication-card {
  border-radius: 0.5rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  padding: 1.5rem;
}

.publication-meta {
  font-size: 0.875rem;
  color: #4b5563;
  margin-bottom: 0.5rem;
}

.publication-title {
  /* font-size: 0.9; */
  font-weight: 400;
  margin-bottom: 0.75rem;
}
.publication-title h2 {
  font-size: 0.5;
  /* font-weight: 400;
  margin-bottom: 0.75rem; */
}

.publication-title a {
  color: inherit;
  text-decoration: none;
}

.publication-title a:hover {
  color: #2563eb;
}

.publication-authors {
  color: #374151;
}

.loading {
  text-align: center;
  padding: 2rem 0;
}
