/* ✅ Modern Hamburger Button */
.hamburger {
  position: fixed;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
  z-index: 1001;
}

/* ✅ Mobile Menu Styles */
.mobile-menu {
  position: fixed;
  top: 0;
  right: -100%;
  width: 75%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  color: white;
  transition: right 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.mobile-menu.open {
  right: 0;
}

/* ✅ Modern "X" Close Button */
.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
}

/* ✅ Navigation Menu Styles */
.mobile-nav-links {
  list-style: none;
  padding: 0;
}

.mobile-nav-links li {
  margin: 15px 0;
}

.mobile-nav-links a {
  color: white;
  font-size: 22px;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.mobile-nav-links a:hover {
  color: #ff6f61;
}
