/* Publication Detail Page Styles */
.publication-detail-page {
  background-color: #f6fcff;
  min-height: 100vh;
}

.header-section {
  padding-top: 120px;
  padding-bottom: 40px;
}

.publication-header {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.breadcrumb {
  margin-bottom: 24px;
  color: #666;
}

.breadcrumb a {
  color: #666;
  text-decoration: none;
}

.breadcrumb a:hover {
  color: #333;
}




.publication-meta {
  margin: 1rem 0;
}

.meta-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.keywords {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0;
}

.keyword-tag {
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  background-color: #f5f5f5;
  text-decoration: none;
  font-size: 0.875rem;
  transition: background-color 0.2s;
}

.keyword-tag:hover {
  background-color: #e0e0e0;
}

.venue {
  margin: 0;
}

.venue-link {
  color: #666;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.2s;
}

.venue-link:hover {
}
  color: #000;

.publication-type {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 16px;
}

.type-tag {
  color: #333;
  text-decoration: none;
  font-weight: 500;
}

.type-tag:hover {
  text-decoration: underline;
}

.publication-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 24px;
  line-height: 1.2;
}



.authors a {
  color: #666;
  text-decoration: none;
}

.authors a:hover {
  color: #333;
}

.content-section {
  padding: 40px 0 80px;
}

.content-layout {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.sidebar .action-buttons {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.view-publication {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #333;
  text-decoration: none;
  font-weight: 500;
}

.export-citation {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  padding: 0;
  text-align: left;
  font-weight: 500;
}

.share-buttons {
  display: flex;
  gap: 12px;
  margin-top: 8px;
}

.share-buttons button {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  color: #666;
  transition: color 0.2s;
}

.share-buttons button:hover {
  color: #333;
}

.abstract {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .content-layout {
    grid-template-columns: 1fr;
  }

  .sidebar {
    order: 2;
  }

  .publication-title {
    font-size: 2rem;
  }

  .share-buttons {
    justify-content: center;
  }
}

/* Loading and Error States */
.loading,
.error {
  text-align: center;
  padding: 100px 20px;
  font-size: 1.2rem;
  color: #666;
}


.member-author {
  color: #0066cc;
  text-decoration: none;
  transition: color 0.2s ease;
}

.member-author:hover {
  color: #004499;
  text-decoration: underline;
}

.authors {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
  margin: 1rem 0;
}

.authors span {
  color: #666;
}
