  }
}

/* ✅ Extra Small Screens */
@media (max-width: 480px) {
  .event-card {
    padding: 8px;
    font-size: 0.8rem;
  }

  .event-details h3 {
    font-size: 0.9rem;
  }

  .date-box {
    width: 50px;
    height: 80px;
  }

  .date-box .month {
    font-size: 10px;
  }

  .date-box .day {
    font-size: 20px;
  }

  .date-box .year {
    font-size: 8px;
  }
}

.footer-contact a:hover,
.visit-details p:hover {
