.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

.navigation {
  margin-bottom: 2rem;
}

.backLink {
  color: #666;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

.backLink:hover {
  color: #ff6f61;
}

.heroSection {
  background-color: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.eventImage {
  width: 100%;
  height: 400px;
  background-color: #e9ecef;
  position: relative;
  overflow: hidden;
}

.eventImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.placeholderImage {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
  color: #adb5bd;
}

.eventHeader {
  padding: 2rem;
}

.eventHeader h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.tagContainer {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.tag {
  background-color: #e7f5ff;
  color: #ff6f61;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

.status {
  background-color: #d4edda;
  color: #28a745;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

.content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
}

.mainContent {
  background-color: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.description h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.description p {
  color: #666;
  line-height: 1.8;
  margin-bottom: 2rem;
}

.schedule {
  border-top: 1px solid #dee2e6;
  padding-top: 2rem;
}

.schedule h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 1rem;
}

.scheduleList {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.scheduleItem {
  display: flex;
  gap: 2rem;
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.scheduleTime {
  font-weight: 600;
  color: #ff6f61;
  min-width: 100px;
}

.scheduleActivity {
  color: #666;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.infoCard {
  background-color: white;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.infoItem {
  display: flex;
  gap: 1rem;
  padding: 1rem 0;
  border-bottom: 1px solid #dee2e6;
}

.infoItem:last-child {
  border-bottom: none;
}

.infoItem svg {
  color: #ff6f61;
  font-size: 1.2rem;
  flex-shrink: 0;
  margin-top: 0.25rem;
}

.infoItem h3 {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 0.25rem;
}

.infoItem p {
  color: #333;
  font-weight: 500;
}

.registerButton {
  background-color: #ff6f61;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
}

.registerButton:hover {
  background-color: #ff6f61;
}

.loadingContainer,
.errorContainer {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 2rem;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #ff6f61;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.backButton {
  display: inline-block;
  background-color: #ff6f61;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  margin-top: 1rem;
  transition: background-color 0.3s ease;
}

.backButton:hover {
  background-color: #ff6f61;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 992px) {
  .content {
    grid-template-columns: 1fr;
  }

  .eventHeader h1 {
    font-size: 2rem;
  }

  .eventImage {
    height: 300px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  .eventHeader {
    padding: 1.5rem;
  }

  .mainContent,
  .infoCard {
    padding: 1.5rem;
  }

  .scheduleItem {
    flex-direction: column;
    gap: 0.5rem;
  }

  .scheduleTime {
    min-width: auto;
  }
}
