/* ProjectDetail.module.css */
.projectDetail {
  max-width: 100%;
  margin: 0 auto;
  padding: 40px 5vw;
}

.projectHeader {
  text-align: center;
  margin-bottom: 60px;
}
.videoContainer {
  margin-top: 20px;
  text-align: center;
}

.projectVideo {
  width: 100%;
  max-width: 800px;
  height: 450px;
  border-radius: 8px;
}

.projectMainImage {
  width: 100%;            /* Image takes full container width */
  height: auto;           /* Height adjusts automatically to preserve aspect ratio */
  max-height: 500px;       /* Remove fixed max-height */
  object-fit: contain;    /* Ensures the whole image is displayed */
  filter: grayscale(100%);
  transition: filter 0.3s ease;
  margin-bottom: 40px;
  align-items: center;
}


.projectMainImage:hover {
  filter: grayscale(0%);
}

.projectTitle {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #000;
}

.projectDescription {
  font-size: 1.2rem;
  line-height: 1.6;
  max-width: 800px;
  margin: 0 auto;
  color: #333;
}

.projectMeta {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  font-size: 1.1rem;
}

.projectType {
  background: #000;
  color: #fff;
  padding: 5px 15px;
  border-radius: 4px;
}

.projectDate {
  color: #666;
}

/* Research Team Section */
.researchTeam {
  margin: 60px 0;
  padding: 40px 0;
  border-top: 2px solid #444;
  border-bottom: 2px solid #444;
}

.researchersTitle {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 40px;
  color: #000;
}

.researchersGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 30px;
}

.researcherCard {
  display: flex;
  align-items: center;
  padding: 20px;
  background: #f8f8f8;
  transition: transform 0.3s ease;
}

.researcherCard:hover {
  transform: translateY(-5px);
}

