.researcherInfo {
  flex: 1;
}

.researcherInfo h3 {
  margin: 0 0 5px 0;
  font-size: 1.2rem;
  color: #000;
}

.researcherRole {
  display: block;
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.researcherAffiliation {
  font-size: 0.9rem;
  color: #444;
  margin: 0;
}

/* Project Content Sections */
.section {
  margin: 60px 0;
  padding: 40px;
  background: #fff;
  border: 1px solid #444;
}

.sectionTitle {
  font-size: 1.8rem;
  margin-bottom: 30px;
  color: #000;
  padding-bottom: 15px;
  border-bottom: 2px solid #444;
}

.sectionContent {
  font-size: 1.1rem;
  line-height: 1.8;
  color: #333;
}

.sectionItems {
  display: grid;
  gap: 30px;
  margin-top: 30px;
}

.sectionItem {
  padding: 30px;
  background: #f8f8f8;
  border-left: 4px solid #000;
}

.stepNumber {
  font-size: 1.2rem;
  font-weight: bold;
  color: #000;
  margin-bottom: 10px;
}

.itemTitle {
  font-size: 1.4rem;
  margin: 10px 0;
  color: #000;
}

.itemDescription {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .projectDetail {
    padding: 20px 5vw;
  }

  .projectTitle {
    font-size: 2rem;
  }

  .section {
    padding: 20px;
  }

  .researchersGrid {
    grid-template-columns: 1fr;
  }

  .projectMeta {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
}

.loadingContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.scrollTarget {
    scroll-margin-top: 2rem; /* Adds some space at the top when scrolling */
    outline: none; /* Removes the outline */
  }
  
  /* Remove the default :target styling */
  .scrollTarget:target {
    outline: none;
    border: none;
  }
