.mainContent {
  margin: 0;
  overflow-x: hidden;
  box-sizing: border-box;
}

.contactSection {
  background: #f5f5f5;
  padding: 50px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.contactText {
  flex: 1;
  text-align: left;
  font-size: 30px;
  font-weight: bold;
}

.contactActions {
  display: flex;
  align-items: center;
  gap: 20px;
}

.emailButton {
  background: white;
  border: 2px solid black;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.emailButton:hover {
  background: black;
  color: white;
  transform: scale(1.05);
}

.socialIcons {
  display: flex;
  gap: 10px;
  margin-right: 80px ;
  align-items: center;
}

.socialIcons a {
  background: black;
  color: white;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 18px;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.socialIcons a:hover {
  background: #ff6f61;
  transform: scale(1.1);
}

.visitSection {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 100%;
  margin: 40px auto;
  padding: 40px;
}

.visitDetails {
  font-size: 18px;
  line-height: 1.6;
  max-width: 600px;
}

.visitDetails h2 {
  font-size: 28px;
  margin-bottom: 10px;
}

.visitDetails p {
  text-decoration: none;
  color: black;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  gap: 10px;
}

.visitDetails p:hover {
  color: #ff6f61;
  transform: scale(1.05);
}

.mapContainer {
  width: 50%;
}

.mapContainer iframe {
  width: 100%;
  height: 300px;
  border: 0;
}

@media (max-width: 768px) {
  .contactSection,
  .visitSection {
    flex-direction: column;
    align-items: flex-start;
  }

  .contactText {
    margin-bottom: 15px;
  }

  .mapContainer {
    width: 100%;
    margin-top: 20px;
  }
}

/* 📌 RESPONSIVE DESIGN */
@media (min-width: 1600px) {
  .contactSection,
  .visitSection {
    max-width: 1600px;
  }
}

@media (max-width: 1024px) {
  .contactSection,
  .visitSection {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .contactText {
    text-align: center;
    margin-bottom: 20px;
  }

  .contactActions {
    flex-direction: column;
    gap: 15px;
  }

  .socialIcons {
    margin-right: 0;
  }

  .mapContainer {
    width: 100%;
    margin-top: 30px;
  }
}



@media (max-width: 480px) {
  .contactText {
    font-size: 24px;
  }

  .emailButton {
    padding: 8px 18px;
    font-size: 14px;
  }

  .socialIcons a {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }

  .mapContainer iframe {
    height: 250px;
  }
}