.collaborationSection {
    text-align: center;
    padding: 0px 10px;
    background-color: white;
  }
  
  .collaborationGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .partnerCard {
    width: 150px; /* Fixed width */
    height: 150px; /* Fixed height */
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 15px;
    transition: box-shadow 0.3s ease;
    cursor: default; /* Prevent clicking effect */
  }
  
  .partnerCard:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Only shadow effect on hover */
  }
  
  .partnerCard img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  /* 📌 RESPONSIVE DESIGN */
@media screen and (min-width: 1600px) {
    .collaborationSection {
      max-width: 1600px;
    }
    
    .partnerCard {
      width: 200px;
      height: 200px;
    }
  }
  
  @media screen and (max-width: 1024px) {
    .partnerCard {
      width: 160px;
      height: 160px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .partnerCard {
      width: 140px;
      height: 140px;
    }
  
    .collaborationGrid {
      gap: 20px;
    }
  }