.title-container {
  text-align: left; /* Aligns text like "Our Members" */
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.section-subtitle {
  font-size: 1.2rem;
  color: #666;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.section-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #222;
  margin: 0;
}
/* 🏆 Award Section Title */
.award-heading {
  font-size: 2.8em;
  text-align: center;
  color: #222;
  margin-bottom: 40px;
  font-weight: bold;
}

.carousel-container {
  width: 90vw; /* Adjusts based on screen width */
  max-width: 1000px;
  height: 60vh; /* Adjusts based on screen height */
  min-height: 100px;
  margin: 0 auto;
  margin-top: 5px;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px; /* Soft round edges */
  background: linear-gradient(
    to right,
    white,
    rgba(255, 255, 255, 0) 20%,
    rgba(255, 255, 255, 0) 80%,
    white
  ); /* Fading edges */
  box-shadow: white;
}

/* ✨ Add an Overlay to Enhance the Fade Effect */
.carousel-container::before,
.carousel-container::after {
  content: "";
  position: absolute;
  top: 0;
  width: 10%;
  height: 100%;
  z-index: 2;
  pointer-events: none;
}

/* Left Gradient Fade */
.carousel-container::before {
  left: 0;
  background: linear-gradient(to right, white, rgba(255, 255, 255, 0));
}

/* Right Gradient Fade */
.carousel-container::after {
  right: 0;
  background: linear-gradient(to left, white, rgba(255, 255, 255, 0));
}

.carousel-track {
  display: flex;
  gap: 2vw;
  transition: transform 1s ease-in-out;
  position: relative;
  width: max-content;
}

/* 📜 Award Cards */
.award-card {
  width: 24vw; /* Adjusts based on screen size */
  max-width: 350px;
  height: 45vh; /* Adjusts based on screen height */
  min-height: 220px; /* Ensures it doesn’t shrink too much */
  background: white;
  border-radius: 10px;
  margin-bottom: 1dvw;
  text-align: center;
  padding: 1.5vw;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  transition: transform 0.6s ease-in-out;
  flex-shrink: 0;
  margin-right: 1vw;

}
.award-card .center-card {
  width: 300px;
  height: 400px;
  transform: scale(1.1);
  opacity: 1;
}

/* 👀 Make the awards visible */
.award-card.visible {
  display: block;
  opacity: 1;
}
/* 🖼️ Award Image */
.award-card img {
  width: 100%;
  height: 30vh; /* Adjust image height based on screen */
  max-height: 250px;
  object-fit: cover;
  background: #ddd;
}
/* 🏆 Award Content */
.award-content {
  padding: 1vw;
  text-align: left;
}

/* 🔤 Award Title */
.award-content h3 {
  font-size: 1.7vw; /* Adjusts dynamically */
  font-weight: bold;
  margin-bottom: 1vh;
}

/* 📝 Award Text */
.award-content p {
  font-size: 1.1vw;
  color: #444;
}

/* 📅 Award Date */
.award-content span {
  font-size: 1vw;
  color: #666;
  display: block;
  margin-top: 1vh;
}

/* 📍 Button */
.more-btn {
  background: darkred;
  color: white;
  font-size: 1.5vw;
  padding: 1vh 2vw;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 2vh;
}

.more-btn:hover {
  background: crimson;
}

/* 📱 Responsive Design for Smaller Screens */
@media (max-width: 768px) {
  .carousel-container {
    height: 60vh;
    width: 95vw;
  }

  .award-card {
    width: 40vw;
    height: 55vh;
    min-height: 380px;
  }

  .award-card img {
    height: 25vh;
  }

  .award-content h3 {
    font-size: 3.5vw;
  }

  .award-content p {
    font-size: 2.5vw;
  }

  .more-btn {
    font-size: 2.5vw;
  }
}
.carousel-wrapper {
  overflow: hidden;
  width: 100%;
}

.carousel-track {
  display: flex;
  transition: transform 0.6s ease-in-out;
  width: max-content;
}
/* 🖥️ LARGE SCREEN OPTIMIZATION (4K SCREENS) */
@media screen and (min-width: 1800px) {
  .section {
    max-width: 1600px;
  }

  .team-section {
    grid-template-columns: repeat(6, 1fr);
  }

  .team-member {
    min-width: 320px;
  }

  .carousel-container {
    max-width: 1600px;
  }

  .award-card {
    max-width: 450px;
  }
}

/* 📌 RESPONSIVE FIXES */
@media screen and (max-width: 1440px) {
  .carousel-container {
    height: 60vh;
  }

  .award-card {
    width: 30vw;
  }

  .more-btn {
    font-size: 1.5vw;
  }
}
/* Modern Fixed Arrows */
.carousel-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px; /* Ensure fixed width */
  height: 50px; /* Ensure fixed height */
  background: rgba(0, 0, 0, 0.6);
  color: white;
  border: none;
  padding: 0; /* Remove extra padding */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%; /* Ensures circular shape */
  font-size: 24px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
  z-index: 10;
}

.left-arrow {
  left: 10px;
}
.right-arrow {
  right: 10px;
}

.carousel-arrow svg {
  width: 20px;
  height: 20px;
}

.carousel-arrow:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: translateY(-50%) scale(1.1);
}

/* Update the carousel track transition */
.carousel-track {
  display: flex;
  gap: 1vw;
  transition: transform 0.6s ease-in-out;
  position: relative;
  width: max-content;
}
