/* Projects.module.css */
.titleContainer {
  text-align: center;
  margin: 40px 0;
  padding: 0 20px;
}

.sectionSubtitle {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #666;
}

.sectionTitle {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: #000;
}

.filterButtons {
  text-align: center;
  margin-bottom: 30px;
}

.filterButton {
  background: black;
  color: white;
  border: 1px solid white;
  padding: 10px 18px;
  margin: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease;
}

.filterButton:hover,
.filterButton.active {
  background: white;
  color: black;
}

.projectsContainer {
  max-width: 100%;
  margin: 40px auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  text-align: center;
  padding: 0 5vw;
}

.projectItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  border-bottom: 2px solid #444;
  padding-bottom: 30px;
  transition: transform 0.3s ease;
  text-decoration: none;
}

.projectItem:nth-child(even) {
  flex-direction: row-reverse;
}

.projectImage {
  width: 35%;
  height: auto;
  filter: grayscale(100%);
  transition: filter 0.3s ease, transform 0.3s ease;
}

.projectItem:hover .projectImage {
  filter: grayscale(0%);
  transform: scale(1.05);
}

.projectText {
  width: 60%;
}

.projectTitle {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #000;
}

.projectDescription {
  font-size: 1.1rem;
  opacity: 0.8;
  max-width: 600px;
  line-height: 1.6;
  color: #333;
}

.readMore {
  text-decoration: none;
  font-weight: bold;
  color: black;
  display: inline-block;
  margin-top: 15px;
  border: 1px solid black;
  padding: 8px 16px;
  transition: background 0.3s ease, color 0.3s ease;
}

.readMore:hover {
  background: black;
  color: white;
}

.hidden {
  display: none;
}

@media (max-width: 768px) {
  .projectsContainer {
    grid-template-columns: 1fr;
  }

  .projectItem,
  .projectItem:nth-child(even) {
    flex-direction: column;
    text-align: center;
  }

  .projectImage,
  .projectText {
    width: 100%;
  }

  .projectTitle {
    margin-top: 20px;
  }
}
/* 📌 Pulse Loader Container */
.pulseContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background: black; /* Matches theme */
}

/* 📌 Pulse Circle */
.pulseCircle {
  width: 50px;
  height: 50px;
  background: rgb(0, 0, 0);
  border-radius: 50%;
  animation: pulse 1.5s infinite ease-in-out;
}

/* 📌 Pulse Animation */
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.3);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* ✅ Responsive Adjustments */
@media (max-width: 768px) {
  .pulseCircle {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 480px) {
  .pulseCircle {
    width: 30px;
    height: 30px;
  }
}
