/* Goals Timeline Styles */
.goals-timeline {
    background-color: black;
    color: white;
    padding: 50px 20px;
    font-family: Arial, sans-serif;
    text-align: center;
    margin: 0;
    box-sizing: border-box;
    width: 100%;
}

.section-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    text-transform: uppercase;
    color: white;
}

.section-description {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 50px;
    color: #dcdcdc;
}

.timeline {
    position: relative;
    margin: 0 auto;
    max-width: 800px;
}

.timeline::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 2px;
    background: #555;
    transform: translateX(-50%);
}

.timeline-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
}

.timeline-item:nth-child(odd) .timeline-content {
    text-align: left;
    transform: translateX(-250%);
}

.timeline-item:nth-child(even) .timeline-content {
    text-align: right;
    transform: translateX(250%);
}
.goals-timeline:hover .timeline-item:nth-child(odd) .timeline-content,
.goals-timeline:hover .timeline-item:nth-child(even) .timeline-content {
    transform: translateX(0);
}

.timeline-content {
    transition: transform 0.6s ease, opacity 0.6s ease;
}



.timeline-icon {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 50px;
    background: #1a1a1a;
    border: 2px solid white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 24px;
    z-index: 10;
}
.timeline-content {
    position: relative;
    margin-top: 30px;
    padding: 20px;
    background: #1a1a1a;
    color: white;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    opacity: 1;
    transform: translateY(50px); /* Start below */
    transition: opacity 0.6s ease, transform 0.6s ease;
}

.timeline-item.visible .timeline-content {
    opacity: 1;
    transform: translateY(0); /* Move to the original position */
}

.timeline-content h3 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.timeline-content p {
    font-size: 16px;
    line-height: 1.6;
    color: #dcdcdc;
}
.hidden {
    opacity: 1;
    transform: translateY(50px); /* Start below */
    transition: opacity 0.6s ease, transform 0.6s ease;
}

.visible {
    opacity: 1;
    transform: translateY(0); /* Move to original position */
    transition: opacity 0.6s ease, transform 0.6s ease;
}

/* No changes needed to previous CSS for .timeline-item */



/* Responsive Design */
@media screen and (max-width: 768px) {
  .timeline::before {
    left: 20px;
  }

  .timeline-item {
    flex-direction: row;
    align-items: flex-start;
  }

  .timeline-icon {
